import React, { useState } from 'react';
import bg from './bg.jpg';
import './App.css';

function App() {

  const [ sended, setSended ] = useState(false)

  const sendEmail = e => {
    e.preventDefault()
    const email = { email: document.getElementById('email').value };

    fetch('https://api.webcreatio.net/avisame.php', {
      method: 'POST',
      mode: 'no-cors',
      body: JSON.stringify(email),
      headers: {
        'Content-Type': 'application/json; charset=UTF-8',
        'Access-Control-Allow-Origin': '*'
      }
    })
    .then(res => {
      if (res) {
        setSended(true);
      }
    })
    .catch(err => console.log(err))
  }

  return (
    <div id="app">
      <img src={bg} alt="background" />
      <span className="content">
        <h1>Airsoft Argentina</h1>
        <h3>Proximamente</h3>
        <div className="formContainer">
          {
            sended ? (
              'Te avisaremos cuando estemos listos!'
            ) : (
              <form onSubmit={sendEmail}>
                Te avisamos cuando estemos en linea ?
                <span className="inputGroup">
                  <input type="email" name="email" id="email" placeholder="IngresÃ¡ tu email" required/>
                  <button type="submit">Avisame!</button>
                </span>
              </form>
            )
          }
        </div>
      </span>
    </div>
  );
}

export default App;
